@font-face {
  font-family: 'Roboto';
  src: url('../fonts/Roboto-Regular.eot');
  src: url('../fonts/Roboto-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/Roboto-Regular.woff2') format('woff2'), url('../fonts/Roboto-Regular.woff') format('woff'), url('../fonts/Roboto-Regular.ttf') format('truetype'), url('../fonts/Roboto-Regular.svg#Roboto-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1240px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -17.5px;
  margin-right: -17.5px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 17.5px;
  margin-right: 17.5px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 35px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

/* Defaults */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: 'Roboto_Reg', sans-serif;
  font-size: 15px;
  color: #333;
}

button:focus {
  outline: none;
}

input:focus {
  outline: none;
}

a {
  color: #004f77;
  text-decoration: none;
}

a:hover {
  color: #001c2b;
}

.wrapper {
  max-width: 1240px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

input[type=submit],
button {
  cursor: pointer;
}

.clearfix {
  display: block;
  clear: both;
}

/* Оставить заявку */

.leftOrder {
  display: block;
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border: none;
  border-radius: 5px;
  font-weight: 500;
  font-size: 18px;
}

.leftOrder_orange {
  background: #FF8A00;
  color: #fff;
}

.leftOrder_white {
  background: #fff;
  color: #333;
  border: 1px solid #FF8A00;
}

section {
  padding: 50px 0;
}

section .section-title {
  margin-bottom: 50px;
}

section .section-title h2 {
  font-size: 48px;
  font-weight: 300;
  text-align: center;
}

section .section-title_lite {
  margin-bottom: 30px;
}

section .section-title_lite h2 {
  font-size: 48px;
  font-weight: 300;
  text-align: center;
  color: #fff;
}

/* Даю согласие */

.formCheck {
  margin-top: 20px;
  margin-left: -25px;
}

.formCheck-input {
  display: none;
}

.formCheck-input:checked + label::before {
  background: url('../img/main/check-icon.png') center center no-repeat;
}

.formCheck label {
  padding-left: 5px;
  font-size: 12px;
}

.formCheck label:before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 10px;
  border: .5px solid #DBDBDB;
  border-radius: 2px;
  background: transparent;
  cursor: pointer;
  vertical-align: middle;
  margin-top: -3px;
}

