/* ======  FAQ  ======= */

.faq {
  background: #00C4DF;
}

.faq .section-title_lite h2 {
  position: relative;
  width: 80%;
  margin: 0 auto;
}

.faq .section-title_lite h2::after {
  content: '';
  display: block;
  width: 100px;
  height: 58px;
  background: url('../img/faq/question-icon.png') no-repeat;
  position: absolute;
  top: 0;
  right: 60px;
}

.faq .faqWrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -17.5px;
  margin-right: -17.5px;
}

.faq .faqBlock {
  box-sizing: border-box;
  margin-left: 17.5px;
  margin-right: 17.5px;
  word-wrap: break-word;
  width: calc(50% - 35px);
  margin-bottom: 30px;
}

.faq .faqBlock-question {
  background: #FFFFFF;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px 5px 0 0;
  padding: 20px 24px;
  font-size: 18px;
  cursor: pointer;
}

.faq .faqBlock-question-arrow {
  float: right;
  cursor: pointer;
  transform: rotate(0deg);
  transition: all .5s;
}

.faq .faqBlock-question-arrow::after {
  content: '';
  display: block;
  clear: both;
}

.faq .faqBlock-question .activeQuestion {
  transform: rotate(180deg);
}

.faq .faqBlock-answer {
  background: #FFFFFF;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0 0 5px 5px;
  padding: 10px 24px 20px;
  font-size: 14px;
  line-height: 20px;
  display: none;
}

