/* Modals  */

.overlay {
  display: none;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3000;
  cursor: pointer;
}

.modal {
  display: none;
  width: 700px;
  padding: 60px;
  box-sizing: border-box;
  background: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3001;
  color: #606060;
}

.modal_close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 25px;
  cursor: pointer;
}

.modal div.form {
  max-width: 450px;
  margin: 0 auto;
}

.modal div.title {
  border-bottom: 1px solid #E0E0E0;
  padding-bottom: 30px;
  padding-left: 70px;
  box-sizing: border-box;
}

.modal_title {
  font-size: 36px;
  color: #002D44;
  margin-bottom: 20px;
}

.modal .modalForm {
  margin-top: 30px;
}

.modal .modalForm .formInput_name {
  margin-bottom: 10px;
}

.modal .modalForm .formInput_input {
  width: 100%;
  font-size: 20px;
  color: #4D6C7C;
  padding: 5px;
  padding-left: 0;
  border: none;
  border-bottom: 1px solid #4D6C7C;
  margin-bottom: 30px;
}

.modal .modalForm .formInput_input::-webkit-input-placeholder {
  font-size: 20px;
  color: #4D6C7C;
}

.modal .modalForm .formInput_input::-moz-placeholder {
  font-size: 20px;
  color: #4D6C7C;
}

.modal .modalForm .formCheck {
  margin-bottom: 20px;
  font-size: 14px;
}

.modal .modalForm .formCheck_input {
  display: none;
}

.modal .modalForm .formCheck_input:checked + label::before {
  background: url('../img/main/checkbox-icon.png') center center no-repeat, #3ABBFF;
}

.modal .modalForm .formCheck label {
  padding-left: 20px;
}

.modal .modalForm .formCheck label:before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-left: -20px;
  margin-right: 10px;
  border-radius: 2px;
  background: #3ABBFF;
  cursor: pointer;
  vertical-align: middle;
  margin-top: -3px;
}

.modal .modalForm .formCheck_link {
  color: #707070;
  text-decoration: underline;
}

.modal .modalForm .formCheck_link:hover {
  text-decoration: none;
}

.modal .modalForm .formBtn {
  padding: 15px 50px;
  background: #0ACD55;
}

.modal .modalForm .formBtn:hover {
  filter: drop-shadow(0 1px 8px #2AEF76);
}

/* libs */

/* Arrows */

.slick-prev,
.slick-next {
  border: 0;
  color: transparent;
  font-size: 0;
  width: 27px;
  height: 82px;
  background: url("../../img/gratitude/slider-arrow.png") no-repeat;
  position: absolute;
  top: 50%;
  z-index: 1;
  transform: translateY(-50%);
  opacity: .8;
}

.slick-prev:focus,
.slick-next:focus {
  outline: 0;
}

.slick-prev:hover,
.slick-next:hover {
  opacity: 1;
  cursor: pointer;
}

.slick-prev {
  left: -50px;
}

.slick-next {
  right: -50px;
  background-position: -27px 0;
}

/* Dots */

.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -20px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  margin: 0 10px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  padding: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid #00C4DF;
  box-sizing: border-box;
  background: #fff;
  color: transparent;
  font-size: 0;
}

.slick-dots li button:hover {
  cursor: pointer;
  opacity: 1;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li.slick-active button {
  background-color: #00C4DF;
  opacity: 1;
  position: relative;
}

.slick-dots li.slick-active button::after {
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid #00C4DF;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

.slick-slide:focus {
  outline: 0;
}

.slick-slide img {
  display: inline-block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

