@import "project.less";
@import "smart-grid.less";


.debug{
    .debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
}


/* Defaults */
.reset();

body {
 .reg();
	font-size: 15px;
	color: @text;
}

button {
			&:focus {
						outline: none
			}
}

input, {
	&:focus {
		outline: none; 
	}
}

a {
		color: @color_link;
		text-decoration: none;
		&:hover {
				color: darken(@color_link, 15%);
		}
}

.wrapper {
	.wrapper();
}

input[type=submit],
button {
	cursor: pointer;
}

.clearfix {
		display: block;
		clear: both;
}


/* Оставить заявку */ 
.leftOrder {
		display: block;
		width: 100%;
		height: 50px;
		line-height: 50px;
		text-align: center;
		border: none;
		border-radius: 5px;
		font-weight: 500;
		font-size: 18px;
		

		&_orange {
				background: @orange;
				color: @white;
		}

		&_white {
				background: @white;
				color: @text;
				border: 1px solid #FF8A00;
		}
}


section {
	padding: 50px 0;

	.section-title {
				margin-bottom: 50px;

				h2 {
					font-size: 48px;
					font-weight: 300;
					text-align: center;
				}
	}

	.section-title_lite {
				margin-bottom: 30px;

				h2 {
					font-size: 48px;
					font-weight: 300;
					text-align: center;
					color: @white;
				}
	}

}


/* Даю согласие */ 
.formCheck {
		margin-top: 20px;
		margin-left: -25px;
	 &-input {
	 	display: none;

	 	&:checked + label::before {
						background: url('../img/main/check-icon.png') center center no-repeat;
			}
	 }

	 label {
	 		padding-left: 5px;
	 		font-size: 12px;

	 		&:before {
	 					content: '';
	 					display: inline-block;
	 					width: 16px;
	 					height: 16px;
	 					margin-right: 10px;
	 					border: .5px solid #DBDBDB;
	 					border-radius: 2px;
	 					background: transparent;
	 					cursor: pointer;
	 					vertical-align: middle;
	 					margin-top: -3px;
	 		}
	 }
}







/* ===================== Blocks ========================= */ 


/* ======  Main  ======= */
.main {
				background: url('../img/main/main-bg.jpg') no-repeat;
				background-size: cover;
				height: 100vh;
				
				.wrapper {
							padding: 37px 0;
				}

				.header {
							.row-flex;

							.logo {
										.col;
										.size(4);
							}

							.address {
										.col;
										.size(4);
									 display: flex;
									 justify-content: center;
									 align-items: center;

									&-icon {
											margin-right: 10px;
									}

									&-text {
											color: @white;
											font-size: 14px;
									}
							}

							.contact {
									.col;
									.size(4);
									display: flex;
								 justify-content: flex-end;
								 align-items: center;

								 .contactLeft {
								 	margin-right: 20px;

								 		&-phone {
								 			font-size: 24px;
								 			font-weight: 500;
								 		}

								 		&-email {
								 			font-size: 14px;
								 			letter-spacing: 0.05em;
								 			text-align: right;
								 		}
								 }

									.contactRight {
												display: flex;
												align-items: center;
												justify-content: center;

												&-link {
													 display: block;
													 font-size: 14px;
													 text-transform: uppercase;
													 line-height: 40px;
														width: 160px;
														height: 40px;
														text-align: center;
														border: 1px solid @white;
														border-radius: 5px;
												}
									}

									a {
										color: @white;
									}
							}
						
				}

				
				.mainBlock {
					padding: 95px 0 50px 0;
					display: flex;
					justify-content: space-between;
							
							&-title {
								font-size: 48px;
								color: @white;
								max-width: 800px;
								cursor: default;
							}
				}



				/* Форма */ 
				.bLockform {
							background: #00B6CF;
							padding: 26px 45px 30px 35px;
							text-align: center;
							color: @white;

							&-title {
									font-size: 36px;
									margin-bottom: 3px;
							}
							&-subtitle {
								 font-size: 14px;
								 margin-bottom: 22px;
							}
				}


				.form {

							&-input {
								margin-bottom: 10px;

										input {
											 width: 100%;
											 height: 50px;
											 background: transparent;
											 font-size: 18px;
											 font-weight: 300;
											 color: #EAF7FF;
											 text-align: center;
											 border: 1px solid #EAF7FF;
											 border-radius: 2px;

											 &::-webkit-input-placeholder {
											 		font-size: 18px;
											 		color: #EAF7FF;
											 		opacity: 0.5;
											 	}

											 &::-moz-placeholder {
											 		font-size: 18px;
											 		color: #EAF7FF;
											 		opacity: 0.5;
											 	}
										}
							}

						&-send {
								margin-top: 15px;
						}

						

				}

}



/* ======  Services  ======= */

.services {
		background: #E5E5E5;

			.servicesWrap {
						.row-flex();
						margin-bottom: 40px;

						.servicesBlock {
										.col;
										.size(3);
										padding: 20px;
										box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
										background: @white;
										display: flex;
										flex-direction: column;

										&-title {
											font-size: 20px;
											text-align: center;
											line-height: 1.1;
											margin-bottom: 10px;

											span {
												display: block;
											}
										}

										&-img {
											margin-bottom: 10px;
										}

									 &-list {
									 	list-style-image: url('../img/services/advant-icon.png') no-repeat;
									 	line-height: 24px;
									 	margin-bottom: 10px;

									 	li {
									 			padding-bottom: 20px;

									 			&::before {
									 				content: '';
									 				display: inline-block;
									 				width: 16px;
									 				height: 12px;
									 				background: url(../img/services/advant-icon.png) no-repeat;
									 				padding-right: 20px;
									 			}
									 	}
									 }

									 a.leftOrder {
									 		margin-top: auto;
									 }
						}

			}

			.servicesAdd {
						max-width: 85%;
						margin: 40px auto 40px;

						.servicesAddBlock {
										background: @white;
										box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
										box-sizing: border-box;
										padding: 20px;
										
										&-img {
												float: left;
												margin-right: 20px;
										}

										&-title {
													font-size: 20px;
													margin-bottom: 12px;
										}

										&-text {
											font-size: 14px;
											margin-bottom: 16px;
										}

										.actions {
													display: flex;

													&-button  {
															width: 250px;
															margin-right: 20px;
													}
										}

						}

						.servicesBottom {
									display: flex;
									justify-content: space-between;

									&-text {
										font-size: 18px;
										text-align: center;
										max-width: 680px;
									}
						}
			}
			
}



/* ======  About  ======= */

.about {
			background: url(../img/about/bg.png) center 2% no-repeat, #00C4DF;
			color: @white;

			&-img {
					float: left;
					margin-right: 20px;
			}

			&-text {
				font-size: 20px;
				line-height: 26px;
				text-indent: 10px;
			}

			.aboutWrap {
						margin-bottom: 30px;
			}

			.aboutAdvantages {
				.row-flex;

				.aboutAdvantage {
							.col;
							.size(3);
							position: relative;

							&-img {
										position: relative;
										top: 50%;
										left: 50%;
										transform: translate(-50%; -50%);
							}

							&-text {
										position: absolute;
										top: 50%;
										left: 0;
										transform: translateY(-55%);
										max-width: 200px;
										font-size: 18px;
										left: 25px;
							}
				}

			}

}

	

/* ======  Advantages  ======= */

.advantages { 
			
			.advantagesWrap {
						.row-flex;
			}

			.advantagesBlock {
					text-align: center;
					margin-bottom: 50px;
					.col();
					.size(4);
					
						&-img {
								
						}

						&-title {
							font-size: 36px;
							font-weight: 300;
							color: #008FA3;
							margin-bottom: 10px;
							position: relative;
							margin-top: 40px;
							padding-top: 20px;

							&::before {
								content: '';
								display: block;
								position: absolute;
								top: 0;
								left: 50%;
								transform: translateX(-50%);
								width: 190px;
								height: 1px;
								background: #008FA3;
							}
						}

						&-text {
							font-size: 18px;
							font-weight: 300;
							max-width: 320px;
							margin: 0 auto;
						}
			}

}
	


/* ======  Steps  ======= */
.steps {
			background: url(../img/steps/steps-bg.png) center center, #00C4DF;
			color: @white;

			.stepsWrap {
					.row-flex;
			}

			.stepsBlock {
					margin-bottom: 30px;
					.col;
					.size(3);


						&-figure {
								font-size: 48px;
								font-weight: bold;
								position: relative;
								margin-bottom: 20px;

								&::after {
									content: "";
									display: block;
									width: 60px;
									height: 4px;
									background: @white;
									margin-top: 10px;
								}
						}

						&-text {
								font-size: 16px;
								line-height: 21px;
						}

			}

			
			.stepsForm {
						max-width: 80%;
						margin: 40px auto 40px auto;
						text-align: center;
						border: 5px solid @white;
						padding: 21px;

						&-title {
							font-size: 36px;
							margin-bottom: 15px;
						}

						&-subtitle {
								font-size: 18px;
								margin-bottom: 15px;
						}

						.stepsFormInputs {
									display: flex;
									justify-content: center;
						}

						.stepsFormInput {
									display: flex;
									align-items: center;
									margin-left: 30px;

									input {
										display: none;

										 	&:checked + label::before {
															background: url('../img/steps/ellips-icon.png') center center no-repeat;
												}
									}

									label {
										cursor: pointer;

										&::before {
													content: '';
													display: inline-block;
													width: 14px;
													height: 14px;
													margin-right: 5px;
													margin-top: -2px;
													border-radius: 50%;
													background: transparent;
													cursor: pointer;
													vertical-align: middle;
													border: 1px solid #FFFFFF;
													box-sizing: border-box;
										}
									}
						}
			}

			.stepsFormMain {
						.row-flex;
						margin-top: 50px;

						.stepsFormMainInput {
									.col;
									.size(4);
									position: relative;

									&-user {
										&::before {
											content: '';
											display: block;
											width: 20px;
											height: 24px;
											background: url(../img/steps/user-icon.png) no-repeat;
											position: absolute;
											top: 0;
											left: 0;
											margin: 15px 10px;
										}
									}

									&-phone {
										&::before {
											content: '';
											display: block;
											width: 24px;
											height: 24px;
											background: url(../img/steps/smartphone-icon.png) no-repeat;
											position: absolute;
											top: 0;
											left: 0;
											margin: 15px 10px;
										}
									}


									input[type="text"],
									input[type="tel"] {
											width: 100%;
											height: 50px;
											background: #F9F9F9;
											border: 0.5px solid #DBDBDB;
											box-sizing: border-box;
											border-radius: 2px;
											padding-left: 45px;

											&::-webkit-input-placeholder {
													font-size: 12px;
													color: #AFAFAF;
												}

											&::-moz-placeholder {
													font-size: 12px;
													color: #AFAFAF;
												}
									}

									.required {
											font-size: 12px;
											font-weight: 300;
											margin-top: 10px;
									}
						}
			}



.formCheck {
		text-align: left;
		margin-left: 10px;
}


}



/* ======  Serve  ======= */

.serve {
			
			.serveWrap {
				.row-flex;
							
							.serveBlock {
										text-align: center;
										padding-bottom: 15px;
										margin-bottom: 40px;
										background: #FBFBFB;
										box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
										.col;
										.size(4);

										&-img {
											margin-bottom: 15px;
										}
										&-title {
											font-size: 24px;
											max-width: 340px;
											margin: 0 auto;
											padding: 5px;
											box-sizing: border-box;
										}
							}


			}

}




/* ======  Gratitude  ======= */

.gratitude {
			

			/* Слайдер */
			.gratitudeSlider {
							margin-bottom: 40px;
			} 



			/* Рассчет стоимости */ 
			.gratitudeCalcWrap {
								background: @white;
								box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
						 	padding: 30px;


						 	.gratitudeCalc {
						 				border: 8px solid #00C4DF;
											box-sizing: border-box;
											padding: 50px 60px;

											&-title {
													font-size: 32px;
													text-align: center;
											}

											
											.gratitudeCalcBLock {
														.row-flex;
														align-items:center;

														&-img {
															.col;
															.size(2);
														}

														&-text {
															.col;
															.size(5);

															font-size: 20px;
															font-style: italic;
															padding-left: 30px;
														}

														&-calc {
																.col;
																.size(5);
																max-width: 234px;
																margin: 0 auto;
														}
											}


						 	}
			}


}



/* ======  FAQ  ======= */

.faq {
		background: #00C4DF;

		.section-title_lite {
					h2 {
						position: relative;
						width: 80%;
						margin: 0 auto;
								&::after {
										content: '';
										display: block;
										width: 100px;
										height: 58px;
										background: url('../img/faq/question-icon.png') no-repeat;
										position: absolute;
										top: 0;
										right: 60px;
								}
					}
		}

	.faqWrap {
				.row-flex;
	}

		.faqBlock {
					.col;
					.size(6);
					margin-bottom: 30px;

						&-question {
									background: #FFFFFF;
									box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
									border-radius: 5px 5px 0 0;
									padding: 20px 24px;
									font-size: 18px;
									cursor: pointer;

									&-arrow {
											float: right;
											cursor: pointer;
											transform: rotate(0deg);
											transition: all .5s;

											&::after {
													content: '';
													display: block;
													clear: both;
											}

									}

									.activeQuestion {
													transform: rotate(180deg);
										}
						}

						&-answer {
								background: #FFFFFF;
								box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
								border-radius: 0 0 5px 5px;
								padding: 10px 24px 20px;
								font-size: 14px;
								line-height: 20px;
								display: none;
						}
		}


}



/* ======  contacts  ======= */

.contacts {
			background: url('../img/contacts/contacts-bg.png') center right no-repeat;



			.contactsWrap{
						.row-flex;

						.contactsLeft {
									.col;
									.size(4);
						}

						.contactsRight {
										.col;
										.size(8);
						}


						.contactsBlock {
										margin-bottom: 40px;
										margin-top: 20px;
										
										&-title {
												font-size: 18px;
												font-weight: 300;
												margin-bottom: 5px;
										}
										
										&-text {
												font-weight: bold;
												font-size: 18px;
												line-height: 25px;
												max-width: 270px;
										}

										.leftOrder {
													max-width: 80%;
													margin-top: 60px;
										}
						}

						.contactsMap {
									width: 716px;
									height: 502px;
									background: url('../img/contacts/macbook.png') no-repeat;
									margin-left: 40px;
						}


			}

}



/* ======  footer  ======= */

.footer {
			background: #333;
			padding: 30px 0;
}












/* Modals  */ 

.overlay {
			display: none;
			width: 100%;
			height: 100%;
			background: rgba(0,0,0, 0.7);

			position: fixed;
			top: 0;
			left: 0;
			z-index: 3000;
			cursor: pointer;
}

.modal {
		 display: none;
			width: 700px;
			padding: 60px;
			box-sizing: border-box;
			background: @white;
			position: fixed;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 3001;
			color: @colorText_modal;

			&_close {
					position: absolute;
					top: 0;
					right: 0;
					padding: 25px;
					cursor: pointer;
					
			}

			div.form {
					max-width: 450px;
					margin: 0 auto;
			}
			div.title {
					border-bottom: 1px solid #E0E0E0;
					padding-bottom: 30px;
					padding-left: 70px;
					box-sizing: border-box;
			}

			&_title {
						font-size: 36px;
						color: @color_link-dark;
						margin-bottom: 20px;
			}

			&_subtitle {

			}

			.modalForm {
							margin-top: 30px;

							.formInput {

										&_name {
												margin-bottom: 10px;
										}

										&_input {
												width: 100%;
												font-size: 20px;
												color: @color_input-modal;
												padding: 5px;
												padding-left: 0;
												border: none;
												border-bottom: 1px solid @color_input-modal;
												margin-bottom: 30px;
												

												&::-webkit-input-placeholder {
														font-size: 20px;
														color: @color_input-modal;
													}

													&::-moz-placeholder {
														font-size: 20px;
														color: @color_input-modal
													}
										}
							}

							.formCheck {
										margin-bottom: 20px;
										font-size: 14px;

										&_input {
													display: none;

													&:checked + label::before {
																background: url('../img/main/checkbox-icon.png') center center no-repeat, @color_checkbox;
													}
										}

										label {
												padding-left: 20px;

												&:before {
															content: '';
															display: inline-block;
															width: 20px;
															height: 20px;
															margin-left: -20px;
															margin-right: 10px;
															border-radius: 2px;
															background: @color_checkbox;
															cursor: pointer;
															vertical-align: middle;
															margin-top: -3px;
												}
										}

										&_link {
													color: #707070;
													text-decoration: underline;

													&:hover {
														text-decoration: none;
													}
										}
							}

							.formBtn {
										padding: 15px 50px;
										background: @green;

										&:hover {
												filter: drop-shadow(0 1px 8px #2AEF76);
										}

							}
			}

}



/* libs */
@import "libs/slick.less";




